import InfoSection from "../../Shared/Components/Sections/SectionTwo";
import "../Style/Guide.css";
import GuidePhoto from "../../assets/Images/Screenshot_20250124_165516_com.qualityconnect 2.png"
import ToggleButtons from "../../Shared/Components/Elements/ToggleButtons";
import QuestionPhoto from "../../assets/Images/Group 24.png";

import { Outlet } from "react-router-dom";
import Aos from "aos";
import "aos/dist/aos.css"; // استيراد CSS الخاصة بـ AOS
import { useEffect } from "react";

const Guide = () => {
    const linksData = [
        { to: "", text: "ارسال واستلام الحوالات" },
        { to: "2", text: "سحب وإيداع الأموال" },
        { to: "3", text: "دفع المشتريات" },
        { to: "4", text: "تسديد الخدمات والفواتير" },
        { to: "5", text: "شحن رصيد" },
      ];
        useEffect(() => {
          Aos.init({
            duration: 800,
            easing: "ease-in-out", 
            once: true,
          });
        }, []);
  return (
    <>
      <InfoSection
        imageSrc={GuidePhoto}
        title=""
        linkText="تعلم  إستخدام الخدمة"
        linkTo="/support"
        className="guide"
        dir={false}
      >
        <span>تعلم </span>  كيفية إستخدام <span>القطيبي شلن</span> لتسهيل أمورك المالي
      </InfoSection>
      <section className="using">
      <div className="section-header">
          <h1 className="title">
            {" "}
            تعلم كيف استخدام <span>القطيبي شلن</span>؟
          </h1>
        </div>
        <ToggleButtons links={linksData} className="custom-class" />

        <Outlet/>

      </section>
      <InfoSection
      imageSrc={QuestionPhoto}
      title="هل توجد لديك المزيد من الأسئلة حول خدماتنا؟"
      linkText="تفقد الدعم الفني"
      linkTo="/support"
      className="question"
      dir={true}
    >
      <>"إذا كان لديك أي أسئلة إضافية حول خدمة إرسال واستلام الحوالات أو كنت بحاجة إلى مزيد من التفاصيل، فلا تتردد في زيارة صفحة الدعم الفني لتجد كل الأسئلة التي تبحث عنها, سنكون سعداء بمساعدتك."</>
     </InfoSection>
    </>
  );
};

export default Guide;
