import { NavLink } from "react-router-dom";
import { useState } from "react";
import "../../Style/ToggleButtons.css";

const ToggleButtons = ({ links, className }: any) => {
  const [isDragging, setIsDragging] = useState(false);
  const [startX, setStartX] = useState(0);
  const [scrollLeft, setScrollLeft] = useState(0);

  const handleMouseDown = (e: React.MouseEvent<HTMLUListElement>) => {
    setIsDragging(true);
    setStartX(e.pageX - e.currentTarget.offsetLeft);
    setScrollLeft(e.currentTarget.scrollLeft);
  };

  const handleMouseLeave = () => {
    setIsDragging(false);
  };

  const handleMouseUp = () => {
    setIsDragging(false);
  };

  const handleMouseMove = (e: React.MouseEvent<HTMLUListElement>) => {
    if (!isDragging) return;
    const x = e.pageX - e.currentTarget.offsetLeft;
    const walk = (x - startX) ; // تعديل سرعة التحرك
    e.currentTarget.scrollLeft = scrollLeft - walk;
  };

  // منع السحب داخل الروابط
  const handleLinkMouseDown = (e: React.MouseEvent) => {
    e.preventDefault();
  };

  return (
    <ul
      className={`toggle_btns ${className} ${isDragging ? "grabbing" : ""}`}
      onMouseDown={handleMouseDown}
      onMouseLeave={handleMouseLeave}
      onMouseUp={handleMouseUp}
      onMouseMove={handleMouseMove}
    >
      {links.map(({ to, text }: any, index: any) => (
        <li key={index}>
          <NavLink
            end
            to={to}
            onMouseDown={handleLinkMouseDown} 
          >
            {text}
          </NavLink>
        </li>
      ))}
    </ul>
  );
};

export default ToggleButtons;
