import { ReactNode } from "react";
import { NavLink } from "react-router-dom";
import "../../Style/SectionTwo.css";

interface InfoSectionProps {
  imageSrc: string;
  title: string;
  linkText: string;
  linkTo: string;
  className?: string;
  children: ReactNode;
  dir: boolean;
}

const InfoSection = ({
  imageSrc,
  title,
  linkText,
  linkTo,
  className = "",
  children,
  dir,
}: InfoSectionProps) => {
  return (
    <section className={`section-two-container ${dir ? "section-right":"section-left"} ${className}`}>
      {dir ? (
        <>
          <div data-aos="fade-left" className="image-wrapper">
            <img src={imageSrc} alt="Section Image" />
          </div>
          <div className="content-wrapper">
            <h1 className="section-title">{title}</h1>
            <p className="section-description">{children}</p>
            <NavLink className="More" to={linkTo}>
              {linkText}
            </NavLink>
          </div>
        </>
      ) : (
        <>
          <div  className="content-wrapper">
            <h1 className="section-title">{title}</h1>
            <p className="section-description">{children}</p>
            <NavLink className="More" to={linkTo}>
              {linkText}
            </NavLink>
          </div>
          <div data-aos="fade-right" className="image-wrapper">
            <img  src={imageSrc} alt="Section Image" />
          </div>
        </>
      )}
    </section>
  );
};

export default InfoSection;
